:root {
  --brand-1: #1877ff;
  --brand-2: #4692ff;
  --brand-3: #bad6ff;
  --black: #000;
  --white: #fff;
  --gray-1: rgba(0, 0, 0, 0.87);
  --gray-2: rgba(0, 0, 0, 0.54);
  --gray-3: rgba(0, 0, 0, 0.4);
  --gray-4: rgba(0, 0, 0, 0.26);
  --gray-5: rgba(0, 0, 0, 0.2);
  --gray-6: rgba(0, 0, 0, 0.1);
  --gray-7: rgba(0, 0, 0, 0.05);
  --gray-8: rgba(0, 0, 0, 0.03);
  --light-1: #f2f4f5;
  --light-2: #fafafa;
  --blue: #39f;
  --gray-dark: #333;
  --green: #62d957;
  --orange: #f70;
  --red: #f56262;
  --yellow: #ffc233;
  --yellow-light: #fff9db;
  --shadow-1: 0 1.5px 3px 0 rgba(0, 0, 0, 0.06), 0 1.5px 3px 0 rgba(0, 0, 0, 0.08);
  --shadow-2: 0 6px 6px 0 rgba(0, 0, 0, 0.03), 0 4px 2px 0 rgba(0, 0, 0, 0.04);
  --shadow-3: 0 6px 6px 0 rgba(0, 0, 0, 0.03), 0 4px 2px 0 rgba(0, 0, 0, 0.04);
  --shadow-4: 0 10px 10px 0 rgba(0, 0, 0, 0.03), 0 8px 4px 0 rgba(0, 0, 0, 0.02);
  --shadow-5: 0 10px 10px 0 rgba(0, 0, 0, 0.03), 0 8px 4px 0 rgba(0, 0, 0, 0.02);
}
.ChatApp,
.MessageContainer,
.Navbar,
.Message .Bubble,
.QuickReplies,
.ChatFooter {
  background-repeat: no-repeat;
  background-size: cover;
}
.ChatApp {
  background-color: var(--light-1);
}
.MessageContainer {
  /*background-image: url(https://pica.zhimg.com/v2-0adeec4d603571488924e7ab3b99b524_r.jpg?source=1940ef5c);*/
}
.Navbar {
  background-color: var(--white);
}
.Navbar .IconBtn {
  color: var(--gray-2);
}
.Navbar-title {
  color: var(--gray-1);
}
.Message.left .Bubble {
  background-color: #fff;
  color: var(--gray-1);
}
.Message.right .Bubble {
  background-color: var(--brand-3);
  color: var(--gray-1);
}
.QuickReplies,
.ChatFooter {
  background-color: rgba(242, 244, 245, 0.95);
}
.Message.left .Bubble {
  border-radius: 4px 20px 20px 20px;
}
.Message.right .Bubble {
  border-radius: 20px 4px 20px 20px;
}
.Card {
  border-radius: 12px;
}
.Btn {
  border-radius: 16px;
}
.QuickReply {
  border-radius: 20px;
}
:root { --brand-1: #1877ff;--brand-2: #4692ff;--brand-3: #bad6ff;--black: #000;--white: #fff;--gray-1: rgba(0,0,0,0.87);--gray-2: rgba(0,0,0,0.54);--gray-3: rgba(0,0,0,0.4);--gray-4: rgba(0,0,0,0.26);--gray-5: rgba(0,0,0,0.2);--gray-6: rgba(0,0,0,0.1);--gray-7: rgba(0,0,0,0.05);--gray-8: rgba(0,0,0,0.03);--light-1: #f2f4f5;--light-2: #fafafa;--blue: #39f;--gray-dark: #333;--green: #62d957;--orange: #f70;--red: #f56262;--yellow: #ffc233;--yellow-light: #fff9db; }